<template>
  <div class="container mx-auto mt-5 space-y-3">
    <div v-for="(r, i) in routiness" :key="i">
      <div class="p-5 text-center bg-gray-200">
        <h2 class="text-2xl font-semibold bangla">{{ r.chapter }}</h2>
        <h2 class="text-xl font-semibold">{{ r.subject }}</h2>

        <div class="flex justify-center gap-3 my-3">
          <p
            class="p-2 font-medium bg-gray-300 border border-gray-400 border-dashed "
          >
            Starts: {{ r.start }}
          </p>
          <p
            class="p-2 font-medium bg-gray-300 border border-gray-400 border-dashed "
          >
            Ends: {{ r.end }}
          </p>
        </div>
      </div>
    </div>
    <div class="mb-10 text-center">
      <button @click="printThis" class="px-6 py-2 font-semibold bg-gray-300">
        Print
      </button>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { routine } from "../plugins/auth";
import _ from "lodash";
import { onMounted } from "@vue/runtime-core";
import { useDateFormat } from "@/hooks/dateFormat";
export default {
  setup() {
    const routiness = ref([]);
    const { formattedDate, formattedDateC } = useDateFormat();
    const printThis = () => {
      window.print();
    };
    const prepareRoutine = async () => {
      let res = await fetch(routine);
      let { routines: shortSyllabus } = await res.json();
      shortSyllabus = shortSyllabus.filter((r) => r.ss22);
      if (shortSyllabus.length > 0) {
        routiness.value = _.filter(
          _.values(
            _.merge(
              _.keyBy(JSON.parse(localStorage.getItem("routines22")), "id"),
              _.keyBy(shortSyllabus, "id"),
              (s) => s.end
            )
          )
        );
      }
    };
    onMounted(() => {
      prepareRoutine();
    });
    return { routiness, formattedDate, formattedDateC, printThis };
  },
};
</script>

